.page {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.skipLink {
  position: absolute;
  top: calc(16px + env(safe-area-inset-top));
  left: calc(16px + env(safe-area-inset-left));
  padding: 10px 16px;
  border-radius: 999px;
  background: #1d1a12;
  color: #fff7e2;
  font-weight: 600;
  transform: translateY(-200%);
  transition: transform 0.2s ease;
  z-index: 20;
}

.skipLink:focus-visible {
  transform: translateY(0);
}

.skipLink:hover {
  transform: translateY(0);
  background: #2c2619;
}

.scene {
  position: absolute;
  inset: 0;
}

.sceneFallback {
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  color: #2c1c08;
  font-weight: 600;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  background: radial-gradient(circle at top, #fdf3cf 0%, #d5e7b2 45%, #b2cf8a 100%);
}

@media (max-width: 900px) {
  .page {
    display: flex;
    flex-direction: column;
  }

  .scene {
    position: relative;
    height: 56vh;
  }
}

@media (prefers-reduced-motion: reduce) {
  .skipLink {
    transition: none;
  }
}
